@import 'settings/_grid';

#grid {
  position: fixed 0 * * 50%;
  transform: translateX(-50%);
  z-index: 2;

  &::before {
    background-color: var(--white);
    display: block;
    padding: 1rem;
    position: fixed 0 * * 0;
    z-index: 1;
  }
}

#grid div.vert {
  background-color: color(green a(0.35));
  lost-column: 1/12 none;
}

#grid div.vert.first-line {
  margin-left: 50px;
}

#grid div.horiz {
  height: 16px;
  border-bottom: 1px solid color(red a(0.35));
}

#grid.grid-1 {
  width: var(--max-width-xs);

  &::before {
    content: 'breakpoint: xs';
  }

  div.vert {
    width: var(--column-width-xs);
  }

  div.vert.first-line {
    margin-left: var(--margin-width-xs);
  }
}

